import {
	mapState, mapMutations
} from 'vuex';
import uCharts from '@/u-charts/u-charts.js';
import {listFamilyBindDevices, listFamilys} from '@/api/address';
import { login, decodeUserInfo, setCurFamilyId, getCurFamilyId } from '@/api/user';
var _self;
var canvasObj = {};
import {devices} from '@/api/device';
// import navbar from '../../components/navbar';
export default {
	// components:{
	// 	navbar
	// },
	data() {
		return {
			showDialog:false,
			cWidth: '',
			cHeight: '',
			tips: '',
			pixelRatio: 1,
			serverData: '',
			itemCount: 10,
			sliderMax: 50,
			devices:[]
		}
	},
	computed:{
		...mapState(["currentAddress", "devices", "currentUser", "address"])
	},
	onLoad() {
		uni.showLoading({})
		uni.setNavigationBarTitle({
		　　title:'首页'
		})
		this.wechatLogin();
		_self = this;
		uni.createSelectorQuery().select(".index-page").boundingClientRect(e=>{
			this.cWidth = e.width;
			this.cHeight = 200;
			this.fillData();
		}).exec();
	},
	onShow(){
		this.showDialog = false;
		if(this.currentAddress){
			listFamilyBindDevices(this.currentAddress.id).then(res=>{
				if(res.data.data && res.data.data.length > 0){
					this.$store.commit("setDevices", res.data.data)
					this.devices = [];
					this.devices.push(...res.data.data);
				} else {
					this.$store.commit("setDevices", [])
					this.devices = [];
				}
			})
		}
		
	},
	onReady() {
		
	},
	methods: {
		...mapMutations(["setCurrentUser", "setAddress", "setcurrentAddress"]),
		wechatLogin(){
			login().then(res=>{
				if(res.firstLogin){
					uni.hideLoading();
					this.showDialog = true;
					this.$store.commit("setCurrentUser", null)
				} else {
					this.$store.commit("setCurrentUser", res);
					return listFamilys(res.OpenId)
				}
				
			}).then(res=>{
				if(res){
					if(res.data.data && res.data.data.length == 0){
						uni.navigateTo({
							url:'../address/addAddress'
						})
					} else {
						let addrs = res.data.data
						getCurFamilyId(this.currentUser.OpenId).then(res=>{
							if(res.data.data){
								let addr = addrs.find(x=>x.id == res.data.data);
								if(addr){
									this.$store.commit("setcurrentAddress", addr)
								}
							} else {
								this.$store.commit("setcurrentAddress", res.data.data[0])
							}
						})
						this.$store.commit("setAddress", res.data.data)
						return listFamilyBindDevices(res.data.data[0].id)
					}
				}
			}).then(res=>{
				if(res){
					if(res.data.data && res.data.data.length > 0){
						this.$store.commit("setDevices", res.data.data)
						this.devices = [];
						this.devices.push(...res.data.data);
					} else {
						this.$store.commit("setDevices", [])
						this.devices = [];
					}
					uni.hideLoading();
				}
			}).catch(res=>{
				uni.hideLoading();
				if(res.errMsg.startsWith("getUserInfo:fail")){
					
					this.showDialog = true;
					this.$store.commit("setCurrentUser", null)
				}
			})
		},
		loginCancel(){
			uni.hideLoading();
			this.showDialog = false;
		},
		loginOk(){
			uni.hideLoading();
			this.showDialog = false;
			uni.navigateTo({
				url:'../login/index'
			})
		},
		OnWechatAuthorized(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		onGetUserInfo(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		switchAddress() {
			uni.navigateTo({
				url: '../address/addressList'
			})
		},
		addDevice(){
			uni.navigateTo({
				url:'../device/addDevice'
			})
		},
		fillData() {
			
			let LineA = {
				categories: [],
				series: []
			};
			LineA.categories = [0, 3, 6, 9, 12, 15, 18, 21, 23];
			LineA.series = [{
				name:'mine',
				data:[20, 25, 25, 30, 20, 25, 25, 30, 20],
				color:'#1890ff',
				type:'line',
				show:true,
				pointShape:'circle',
				legendShape:'line'
			}, {
				name:'average',
				data:[15, 20, 21, 19, 15, 20, 21, 19, 15],
				color:'#2fc25b',
				type:'line',
				show:true,
				pointShape:'circle',
				legendShape:'line'
			}, {
				name:'out_door',
				data:[10, 15, 17, 8, 10, 15, 17, 8, 10],
				color:'#facc14',
				type:'line',
				show:true,
				pointShape:'circle',
				legendShape:'line'
			}];
			this.showLineA("canvasLineA", LineA);
		},
				
		showLineA(canvasId, chartData) {
			canvasObj[canvasId] = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'line',
				fontSize: 11,
				padding:[15,15,15,15],
				legend:{
					show:true,
					padding:5,
					lineHeight:11,
					margin:5,
					format:val=>{
						return val+'μg/m³';
					}
				},
				dataLabel: false,
				dataPointShape: false,
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: false,
				enableScroll: true, //开启图表拖拽功能
				xAxis: {
					itemCount: 30,
					scrollAlign: 'left',
				},
				yAxis: {
					min: 10,
					max: 60,
					format: (val) => {
						return Math.floor(val);
					}
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				dataLabel: true,
				dataPointShape: true
			});
		},	
		touchLineA(e) {
			canvasObj['canvasLineA'].scrollStart(e);
		},
		moveLineA(e) {
			canvasObj['canvasLineA'].scroll(e);
		},
		touchEndLineA(e) {
			canvasObj['canvasLineA'].scrollEnd(e);
			//下面是toolTip事件，如果滚动后不需要显示，可不填写
			canvasObj['canvasLineA'].showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		},
		
	},
	computed: {
		...mapState(["currentAddress", "currentUser"])
	},
	mounted() {
		uni.setNavigationBarTitle({
			title: '首页'
		})
	},
}
